import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ViewPractiseComponent } from './view-practise/view-practise.component';
import { MainPageComponent } from './main-page/main-page.component';
import { H5AndCssRWDPractiseComponent } from './h5-and-css-rwdpractise/h5-and-css-rwdpractise.component';
import { GymComponent } from './gym/gym.component';

const routes: Routes = [
  {path:'',component:MainPageComponent},
  {path:'viewPractise',component:ViewPractiseComponent},
  {path:'RwdPractise',component:H5AndCssRWDPractiseComponent},
  {path:'gymPractise',component:GymComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }

