import { Component, OnInit } from '@angular/core';
import { ScrollToService, ScrollToConfigOptions } from '@nicky-lenaers/ngx-scroll-to';
import * as $ from 'jquery';
@Component({
  selector: 'app-main-page',
  templateUrl: './main-page.component.html',
  styleUrls: ['./main-page.component.css']
})
export class MainPageComponent implements OnInit {

  constructor(private _scrollToService: ScrollToService){
   
  }

  ngOnInit() {
    
  }

  public triggerScrollTo(event,val) {
    const config: ScrollToConfigOptions = {
      target: val,
      offset: -20
    };
    
    this._scrollToService.scrollTo(config);

    let el:HTMLElement = document.getElementById('navButton') as HTMLElement;
    el.click();     
  }

  title = 'ProfileAngular';
  year = new Date().getFullYear();
  public isHovering = false;
  public IsRwd = false;
  status= true;
   sideClass() 
  {return{
    'col-1':true,
    'text-success':this.status,
    'test':this.IsRwd,
    'd-none':true
    
  }
  };
  onResize(event) {
    console.log(event);
}
onMouseout(event){
  
  this.isHovering =true;
  this.status =false;
}
onMouseover(event){
  if(event.x<20 && this.IsRwd){
    this.isHovering =false;
    this.status =true;
  }else{
    this.isHovering =this.IsRwd?true:false;
    this.status =true;
  }
}


}
