import { Component, OnInit, HostListener } from '@angular/core';
// declare var jQuery: any;
declare var Swiper: any;


function Swiper3D() {
      var swiper = new Swiper('.swiper-container', {
        effect: 'coverflow',
        grabCursor: true,
        centeredSlides: true,
        slidesPerView: 'auto',
         loop: true,
        coverflowEffect: {
          rotate: 50,
          stretch: 0,
          depth: 100,
          modifier: 1,
          slideShadows : true,
        },
        pagination: {
          el: '.swiper-pagination',
        },
      });
}

function setCountDown(){
      var targetDay =new Date('Jan 1,2021 00:00:00').getTime();
      // newDay(targetDay);
      setInterval(function(){
        newDay(targetDay)},1000)
      ;
}

function newDay(targetDay){

    var now = new Date().getTime();
    var gap = targetDay-now;
    // console.log(gap);
      var second = 1000;
      var minute = second*60;
      var hour = minute*60;
      var day = hour*24;
      var d = Math.floor(gap/day);
      // console.log(d);
      var h = Math.floor((gap %day)/hour);
      // console.log(h);
      var m = Math.floor((gap %hour)/minute);
      // console.log(m);
      var s = Math.floor((gap %minute)/second);
      // console.log(s);
      
      document.getElementById('day').innerText=d.toString();
      document.getElementById('hour').innerText=h.toString();
      document.getElementById('min').innerText=m.toString();
      document.getElementById('sec').innerText=s.toString();
}

@Component({
  selector: '',
  templateUrl: './view-practise.component.html',
  styleUrls: ['./view-practise.component.css','./selfAnimation.css']
})

export class ViewPractiseComponent implements OnInit {
  status: string = "red";
  
  constructor() { }

  ngOnInit() {
    Swiper3D();
    setCountDown();
  }
  @HostListener('window:scroll', ['$event']) // for window scroll events
  onScroll(event) {
    var value = window.scrollY;
    let bg = document.getElementById('bg');
  let moon = document.getElementById('moon');
  let mountain = document.getElementById('mountain');
  let road = document.getElementById('road');
  let text = document.getElementById('text');
  bg.style.top= value *0.5+"px";
    moon.style.left= -value * 0.5+"px";
    mountain.style.top= -value * 0.15+"px";
    road.style.top= value * 0.15+"px";
    text.style.top= value * 1+"px";
  }

  onClickColor(selectColor){
    console.log("ssssss",selectColor);
    this.status=selectColor;
  }
}
